@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  margin:0;
  padding: 0;

  font-family: 'Poppins', sans-serif;
}
.calc-container {
    max-width: 600px;
    background-color: whitesmoke;
    
    margin:auto;
    margin-top: 100px;
    height: 400px;

    padding: 25px;

    border-radius: 20px;
    font-size: 30px;
}

.calc-container .header {
    font-size: 30px;
}

.calc-container .input {
    margin-top: 50px;
    margin-bottom: 20px;
    font-size:30px;

    width: 100%;

    border: none;
    border-bottom: 1px solid #333;
    background-color: whitesmoke;
}

.calc-container .input:focus {
    outline: none;
}

.big {
    font-size: 40px;
}

.small {
    font-size: 20px;
}
